import React from "react";
import { Link, useLocation, useHistory } from "react-router-dom";
import { fade, makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import InputBase from "@material-ui/core/InputBase";
import Badge from "@material-ui/core/Badge";
import { Container, Box, Button } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import MailIcon from "@material-ui/icons/Mail";
import NotificationsIcon from "@material-ui/icons/Notifications";
import { Search, ShoppingCart, ArrowBackIos } from "@material-ui/icons";

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: "block",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(3),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  inputRoot: {
    color: "inherit",
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "20ch",
    },
  },
  sectionDesktop: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "flex",
    },
  },
  sectionDesktopBlock: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "inline",
    },
  },
  sectionMobile: {
    display: "flex",
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
  },
}));

export default function PrimarySearchAppBar() {
  const classes = useStyles();
  const location = useLocation();
  const history = useHistory();

  return (
    <div className={classes.grow}>
      {location.pathname === "/" || location.pathname === "" ? (
        <AppBar position="fixed">
          <Toolbar disableGutters>
            <Container maxWidth="md">
              <Box display="flex" alignItems="center">
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                  component={Link}
                  to="/"
                >
                  <Typography className={classes.title} variant="h6" noWrap>
                    IDNStyle
                  </Typography>
                </IconButton>
                <div className={classes.sectionDesktop}>
                  <div className={classes.search}>
                    <div className={classes.searchIcon}>
                      <SearchIcon />
                    </div>
                    <InputBase
                      placeholder="Searchâ€¦"
                      classes={{
                        root: classes.inputRoot,
                        input: classes.inputInput,
                      }}
                      inputProps={{ "aria-label": "search" }}
                    />
                  </div>
                </div>
                <div className={classes.grow} />
                <div className={classes.sectionMobile}>
                  <Search />
                </div>
                <div>
                  <IconButton
                    aria-label="show 4 new mails"
                    color="inherit"
                    className={classes.sectionDesktopBlock}
                  >
                    <Badge badgeContent={4} color="secondary">
                      <MailIcon />
                    </Badge>
                  </IconButton>
                  <IconButton
                    aria-label="show 17 new notifications"
                    color="inherit"
                    component={Link}
                    to="/notif"
                  >
                    <Badge badgeContent={17} color="secondary">
                      <NotificationsIcon />
                    </Badge>
                  </IconButton>
                  <IconButton aria-label="show 4 new mails" color="inherit">
                    <Badge badgeContent={5} color="secondary">
                      <ShoppingCart />
                    </Badge>
                  </IconButton>
                </div>
              </Box>
            </Container>
          </Toolbar>
        </AppBar>
      ) : location.pathname === "/profile" ||
        location.pathname === "profile" ||
        location.pathname.split("/")[1] === "u" ? (
        <AppBar position="fixed">
          <Toolbar disableGutters>
            <Container maxWidth="md">
              <Box display="flex" alignItems="center">
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                  onClick={history.goBack}
                >
                  <ArrowBackIos />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                  Profile
                </Typography>
              </Box>
            </Container>
          </Toolbar>
        </AppBar>
      ) : location.pathname === "/notif" || location.pathname === "notif" ? (
        <AppBar position="fixed">
          <Toolbar disableGutters>
            <Container maxWidth="md">
              <Box display="flex" alignItems="center">
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                  onClick={history.goBack}
                >
                  <ArrowBackIos />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                  Notifikasi
                </Typography>
              </Box>
            </Container>
          </Toolbar>
        </AppBar>
      ) : location.pathname === "/login" || location.pathname === "/daftar" ? (
        <AppBar position="fixed">
          <Toolbar disableGutters>
            <Container maxWidth="md">
              <Box display="flex" alignItems="center">
                <IconButton
                  edge="start"
                  className={classes.menuButton}
                  color="inherit"
                  aria-label="menu"
                  onClick={history.goBack}
                >
                  <ArrowBackIos />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                  IDNStyle
                </Typography>
                {location.pathname === '/login' ?
                <Button
                  style={{ marginLeft: "auto" }}
                  color="inherit"
                  component={Link}
                  to="/daftar"
                >
                  Daftar
                </Button>
                :
                <Button
                  style={{ marginLeft: "auto" }}
                  color="inherit"
                  component={Link}
                  to="/login"
                >
                  Login
                </Button>}
              </Box>
            </Container>
          </Toolbar>
        </AppBar>
      ) : null}
    </div>
  );
}
